.section-card-aboutMe{
    height: 100%;
    position: relative;
    -webkit-animation-name: slideIn;
            animation-name: slideIn;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.section-card-edu{
    max-width: 100%;
    height: 100%;
    position: relative;
    -webkit-animation-name: slideIn;
            animation-name: slideIn;
